import React, { useEffect, useRef } from 'react';
import gsap from 'gsap';
import './IntroAnimation.css';

function IntroAnimation({ onComplete }) {
  const containerRef = useRef(null);
  const svgRef = useRef(null);

  useEffect(() => {
    // Check if we've already shown the intro this session
    const hasSeenIntro = sessionStorage.getItem('bigflix-intro-seen');
    
    if (hasSeenIntro) {
      onComplete();
      return;
    }

    // Small delay to ensure DOM is ready
    const timer = setTimeout(() => {
      startAnimation();
    }, 50);

    const startAnimation = () => {
      if (!svgRef.current) return;

      // Make SVG visible
      svgRef.current.style.visibility = 'visible';

      // B Animation
      const b_tl = gsap.timeline();
      b_tl
        .from("#B1-base", { scaleY: 0, transformOrigin: "50% 100%", duration: 0.2 }, 0)
        .from("#B2", { scaleX: 0, transformOrigin: "0% 50%", duration: 0.1 }, 0.1)
        .from("#B3", { scaleX: 0, transformOrigin: "0% 50%", duration: 0.1 }, 0.15)
        .from("#B4", { scaleX: 0, transformOrigin: "0% 50%", duration: 0.1 }, 0.2)
        .to("#B1-shadow", { opacity: 0, duration: 0.5 }, 0);

      // I1 Animation
      const i1_tl = gsap.timeline();
      i1_tl
        .from("#I1", { scaleY: 0, transformOrigin: "50% 100%", duration: 0.15 }, 0);

      // G Animation
      const g_tl = gsap.timeline();
      g_tl
        .from("#G1-base", { scaleY: 0, transformOrigin: "50% 100%", duration: 0.18 }, 0)
        .from("#G2", { scaleX: 0, transformOrigin: "100% 50%", duration: 0.1 }, 0.08)
        .from("#G3", { scaleX: 0, transformOrigin: "0% 50%", duration: 0.1 }, 0.14)
        .from("#G4", { scaleX: 0, transformOrigin: "100% 50%", duration: 0.08 }, 0.2)
        .to("#G1-shadow", { opacity: 0, duration: 0.5 }, 0);

      // F Animation
      const f_tl = gsap.timeline();
      f_tl
        .from("#F1", { scaleX: 0, duration: 0.12 }, 0)
        .from("#F2-base", { scaleY: 0, duration: 0.25 }, 0.08)
        .from("#F3", { scaleX: 0, duration: 0.12 }, 0.2)
        .to("#F2-shadow", { opacity: 0, duration: 0.5 });

      // L Animation
      const l_tl = gsap.timeline();
      l_tl
        .from("#L1-base", { scaleY: 0, duration: 0.18 }, 0)
        .from("#L2", { scaleX: 0, transformOrigin: "0% 50%", duration: 0.12 }, 0.15)
        .to("#L1-shadow", { opacity: 0, duration: 0.5 });

      // I2 Animation
      const i2_tl = gsap.timeline();
      i2_tl
        .from("#I2", { scaleY: 0, transformOrigin: "50% 100%", duration: 0.15 }, 0);

      // X Animation
      const x_tl = gsap.timeline();
      x_tl
        .from("#X1", { scaleY: 0, transformOrigin: "50% 0%", duration: 0.3 }, 0)
        .from("#X1", { opacity: 0, duration: 0.08 }, 0)
        .from("#X2-base", { scaleY: 0, transformOrigin: "50% 0%", duration: 0.25 }, 0.08)
        .from("#X2-base", { opacity: 0, duration: 0.01 }, 0.08)
        .to("#X2-shadow", { opacity: 0, duration: 0.8 }, 0);

      // Movement Timeline - slide in from right
      const movement_tl = gsap.timeline();
      movement_tl
        .from(svgRef.current, { opacity: 0, duration: 0.4 }, 0)
        .from(svgRef.current, { 
          x: 200,
          duration: 1.2, 
          ease: "power2.out" 
        }, 0);

      // Exit Timeline
      const exit_tl = gsap.timeline();
      exit_tl
        .to(svgRef.current, { opacity: 0, scale: 1.1, duration: 0.4, ease: "power2.in" })
        .to(containerRef.current, { opacity: 0, duration: 0.3 }, "-=0.1");

      // Master Timeline (runs once)
      const master_tl = gsap.timeline({
        onComplete: () => {
          sessionStorage.setItem('bigflix-intro-seen', 'true');
          onComplete();
        }
      });
      
      master_tl
        .add(movement_tl, 0)
        .add(b_tl, 0.3)
        .add(i1_tl, 0.45)
        .add(g_tl, 0.55)
        .add(f_tl, 0.75)
        .add(l_tl, 0.9)
        .add(i2_tl, 1.05)
        .add(x_tl, 1.15)
        .add(exit_tl, 2.5); // Exit after 2.5 seconds total
    };

    return () => {
      clearTimeout(timer);
      gsap.killTweensOf("*");
    };
  }, [onComplete]);

  // Skip if already seen
  if (sessionStorage.getItem('bigflix-intro-seen')) {
    return null;
  }

  return (
    <div className="bigflix-container" ref={containerRef}>
      <svg 
        ref={svgRef}
        className="bigflix-svg" 
        width="867" 
        height="233" 
        viewBox="0 0 867 233" 
        fill="none" 
        xmlns="http://www.w3.org/2000/svg"
      >
        {/* B */}
        <g id="B">
          <path id="B1-base" className="base" d="M0 0H36.4V232.8H0V0Z" />
          <path id="B1-shadow" d="M0 0H36.4V232.8H0V0Z" fill="url(#B1-shadowFill)" />
          <path id="B2" className="base" d="M36.4 0H85C105 0 120 15 120 40C120 65 105 80 85 85H36.4V0Z" />
          <path id="B3" className="base" d="M36.4 85H90C115 85 130 105 130 130C130 160 110 180 85 180H36.4V85Z" />
          <path id="B4" className="base" d="M36.4 180H90C115 180 130 195 130 215C130 225 120 232.8 100 232.8H36.4V180Z" />
          <defs>
            <linearGradient id="B1-shadowFill" x1="50%" y1="0%" x2="50%" y2="100%" gradientUnits="userSpaceOnUse">
              <stop className="shadow-start" offset="0%" />
              <stop className="shadow-end" offset="100%" />
            </linearGradient>
          </defs>
        </g>
        
        {/* I (first) */}
        <path id="I1" className="base" d="M160 0H196.4V232.8H160V0Z" />
        
        {/* G */}
        <g id="G">
          <path id="G1-base" className="base" d="M226 0H262.4V232.8H226V0Z" />
          <path id="G1-shadow" d="M226 0H262.4V232.8H226V0Z" fill="url(#G1-shadowFill)" />
          <path id="G2" className="base" d="M262.4 0H330C360 0 380 25 380 60V80H343.6V65C343.6 45 330 36.4 310 36.4H262.4V0Z" />
          <path id="G3" className="base" d="M262.4 196.4H310C330 196.4 343.6 187 343.6 167V152H380V175C380 210 355 232.8 315 232.8H262.4V196.4Z" />
          <path id="G4" className="base" d="M300 115H380V151.4H300V115Z" />
          <defs>
            <linearGradient id="G1-shadowFill" x1="50%" y1="0%" x2="50%" y2="100%" gradientUnits="userSpaceOnUse">
              <stop className="shadow-start" offset="0%" />
              <stop className="shadow-end" offset="100%" />
            </linearGradient>
          </defs>
        </g>
        
        {/* F */}
        <g id="F">
          <path id="F3" className="base" d="M516 84H430V120H516V84Z" />
          <path id="F2-base" className="base" d="M429.8 0V232.8H466.8V0H429.8Z" />
          <path id="F2-shadow" d="M429.8 0V232.8H466.8V0H429.8Z" fill="url(#F2-shadowFill)" />
          <path id="F1" className="base" d="M531.8 0H429.8V36.4H531.8V0Z" />
          <defs>
            <linearGradient id="F2-shadowFill" x1="50%" y1="0%" x2="50%" y2="40%" gradientUnits="userSpaceOnUse">
              <stop className="shadow-start" offset="0%" />
              <stop className="shadow-end" offset="100%" />
            </linearGradient>
          </defs>
        </g>
        
        {/* L */}
        <g id="L">
          <path id="L1-base" className="base" d="M555.5 0H591.9V232.8H555.5V0Z" />
          <path id="L1-shadow" d="M555.5 0H591.9V232.8H555.5V0Z" fill="url(#L1-shadowFill)" />
          <path id="L2" className="base" d="M555.5 196.4L654.5 196.4V232.8L555.5 232.8V196.4Z" />
          <defs>
            <linearGradient id="L1-shadowFill" x1="50%" y1="100%" x2="50%" y2="0%" gradientUnits="userSpaceOnUse">
              <stop className="shadow-start" offset="0%" />
              <stop className="shadow-end" offset="100%" />
            </linearGradient>
          </defs>
        </g>
        
        {/* I (second) */}
        <path id="I2" className="base" d="M684.2 0H720.7V232.8H684.2V0Z" />
        
        {/* X */}
        <g id="X">
          <path id="X2-base" className="base" d="M752 0L840.1 227.2L881.8 232.8L791.5 0H752Z" />
          <path id="X2-shadow" d="M752 0L840.1 227.2L881.8 232.8L791.5 0H752Z" fill="url(#X2-shadowFill)" />
          <path id="X1" className="base" d="M748.1 216.8L786.5 220.8L881.8 0H841.7L748.1 216.8Z" />
          <defs>
            <linearGradient id="X2-shadowFill" x1="0%" y1="10%" x2="80%" y2="100%" gradientUnits="objectBoundingBox">
              <stop className="shadow-end" offset="0%" />
              <stop className="shadow-start" offset="50%" />
              <stop className="shadow-end" offset="100%" />
            </linearGradient>
          </defs>
        </g>
      </svg>
    </div>
  );
}

export default IntroAnimation;
