import React, { useState, useEffect } from 'react';
import { useAuth } from '../contexts/AuthContext';
import { requestsApi } from '../services/api';
import './Requests.css';

const TMDB_IMAGE_BASE = 'https://image.tmdb.org/t/p/w200';

function Requests() {
  const { user } = useAuth();
  const [requests, setRequests] = useState([]);
  const [loading, setLoading] = useState(true);
  const [filter, setFilter] = useState('all');
  const [message, setMessage] = useState({ type: '', text: '' });
  const [processingId, setProcessingId] = useState(null);

  useEffect(() => {
    fetchRequests();
  }, [filter]);

  const fetchRequests = async () => {
    setLoading(true);
    try {
      const params = filter !== 'all' ? { status: filter } : {};
      const response = await requestsApi.getRequests(params);
      setRequests(response.data);
    } catch (error) {
      console.error('Failed to fetch requests:', error);
      setMessage({ type: 'error', text: 'Failed to load requests' });
    } finally {
      setLoading(false);
    }
  };

  const handleApprove = async (request) => {
    setProcessingId(request.id);
    try {
      await requestsApi.approveRequest(request.id, {});
      setMessage({ type: 'success', text: `${request.title} approved and added to download queue` });
      fetchRequests();
    } catch (error) {
      setMessage({ type: 'error', text: error.response?.data?.error || 'Failed to approve request' });
    } finally {
      setProcessingId(null);
    }
  };

  const handleReject = async (request, notes = '') => {
    setProcessingId(request.id);
    try {
      await requestsApi.rejectRequest(request.id, { notes });
      setMessage({ type: 'success', text: `Request for ${request.title} rejected` });
      fetchRequests();
    } catch (error) {
      setMessage({ type: 'error', text: error.response?.data?.error || 'Failed to reject request' });
    } finally {
      setProcessingId(null);
    }
  };

  const handleDelete = async (request) => {
    if (!window.confirm('Are you sure you want to delete this request?')) return;
    
    setProcessingId(request.id);
    try {
      await requestsApi.deleteRequest(request.id);
      setMessage({ type: 'success', text: 'Request deleted' });
      fetchRequests();
    } catch (error) {
      setMessage({ type: 'error', text: error.response?.data?.error || 'Failed to delete request' });
    } finally {
      setProcessingId(null);
    }
  };

  const getStatusBadge = (status) => {
    switch (status) {
      case 'pending':
        return <span className="status-badge pending">Pending</span>;
      case 'approved':
        return <span className="status-badge approved">Approved</span>;
      case 'rejected':
        return <span className="status-badge rejected">Rejected</span>;
      case 'downloaded':
        return <span className="status-badge downloaded">Downloaded</span>;
      default:
        return <span className="status-badge">{status}</span>;
    }
  };

  return (
    <div className="requests-page">
      <div className="page-header">
        <h1>Requests</h1>
        <p>{user?.role === 'admin' ? 'Manage content requests' : 'Your content requests'}</p>
      </div>

      <div className="filter-bar">
        {['all', 'pending', 'approved', 'rejected'].map(status => (
          <button
            key={status}
            className={`filter-btn ${filter === status ? 'active' : ''}`}
            onClick={() => setFilter(status)}
          >
            {status.charAt(0).toUpperCase() + status.slice(1)}
          </button>
        ))}
      </div>

      {message.text && (
        <div className={`message ${message.type}`}>
          {message.text}
        </div>
      )}

      {loading ? (
        <div className="loading">
          <div className="spinner"></div>
        </div>
      ) : requests.length === 0 ? (
        <div className="empty-state">
          <span className="empty-icon"></span>
          <p>No {filter !== 'all' ? filter : ''} requests found</p>
        </div>
      ) : (
        <div className="requests-list">
          {requests.map(request => (
            <div key={request.id} className="request-card">
              <div className="request-poster">
                {request.posterPath ? (
                  <img 
                    src={`${TMDB_IMAGE_BASE}${request.posterPath}`}
                    alt={request.title}
                  />
                ) : (
                  <div className="no-poster">
                    {request.contentType === 'tv' ? '' : ''}
                  </div>
                )}
              </div>

              <div className="request-info">
                <div className="request-header">
                  <h3>{request.title}</h3>
                  {getStatusBadge(request.status)}
                </div>
                
                <div className="request-meta">
                  <span className="meta-type">
                    {request.contentType === 'tv' ? ' TV Show' : ' Movie'}
                  </span>
                  {request.year && <span className="meta-year">{request.year}</span>}
                  {request.serverName && (
                    <span className="meta-server"> {request.serverName}</span>
                  )}
                </div>

                {request.seasons && (
                  <div className="request-seasons">
                    Seasons: {request.seasons.join(', ')}
                  </div>
                )}

                <div className="request-details">
                  <span>Requested by <strong>{request.requestedByUsername}</strong></span>
                  <span>{new Date(request.requestedAt).toLocaleDateString()}</span>
                </div>

                {request.notes && (
                  <div className="request-notes">
                    <strong>Notes:</strong> {request.notes}
                  </div>
                )}
              </div>

              <div className="request-actions">
                {user?.role === 'admin' && request.status === 'pending' && (
                  <>
                    <button
                      className="btn btn-approve"
                      onClick={() => handleApprove(request)}
                      disabled={processingId === request.id}
                    >
                       Approve
                    </button>
                    <button
                      className="btn btn-reject"
                      onClick={() => handleReject(request)}
                      disabled={processingId === request.id}
                    >
                       Reject
                    </button>
                  </>
                )}
                
                {(user?.role === 'admin' || request.status === 'pending') && (
                  <button
                    className="btn btn-delete"
                    onClick={() => handleDelete(request)}
                    disabled={processingId === request.id}
                  >
                    
                  </button>
                )}
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}

export default Requests;
