import React, { useState } from 'react';
import { useAuth } from '../contexts/AuthContext';
import { authApi, usersApi } from '../services/api';
import './Settings.css';

function Settings() {
  const { user, logout } = useAuth();
  const [activeTab, setActiveTab] = useState('profile');
  const [message, setMessage] = useState({ type: '', text: '' });
  const [saving, setSaving] = useState(false);

  // Profile form state
  const [profileForm, setProfileForm] = useState({
    email: user?.email || ''
  });

  // Password form state
  const [passwordForm, setPasswordForm] = useState({
    currentPassword: '',
    newPassword: '',
    confirmPassword: ''
  });

  const handleProfileSubmit = async (e) => {
    e.preventDefault();
    setSaving(true);
    try {
      await usersApi.updateUser(user.id, { email: profileForm.email });
      setMessage({ type: 'success', text: 'Profile updated successfully' });
    } catch (error) {
      setMessage({ type: 'error', text: error.response?.data?.error || 'Failed to update profile' });
    } finally {
      setSaving(false);
    }
  };

  const handlePasswordSubmit = async (e) => {
    e.preventDefault();
    
    if (passwordForm.newPassword !== passwordForm.confirmPassword) {
      setMessage({ type: 'error', text: 'New passwords do not match' });
      return;
    }

    if (passwordForm.newPassword.length < 8) {
      setMessage({ type: 'error', text: 'Password must be at least 8 characters' });
      return;
    }

    setSaving(true);
    try {
      await authApi.changePassword({
        currentPassword: passwordForm.currentPassword,
        newPassword: passwordForm.newPassword
      });
      setMessage({ type: 'success', text: 'Password changed successfully' });
      setPasswordForm({ currentPassword: '', newPassword: '', confirmPassword: '' });
    } catch (error) {
      setMessage({ type: 'error', text: error.response?.data?.error || 'Failed to change password' });
    } finally {
      setSaving(false);
    }
  };

  const handleLogoutAllDevices = async () => {
    if (!window.confirm('This will log you out of all devices. Continue?')) return;
    
    try {
      await authApi.logout();
      logout();
    } catch (error) {
      setMessage({ type: 'error', text: 'Failed to logout' });
    }
  };

  return (
    <div className="settings-page">
      <div className="page-header">
        <h1>Settings</h1>
        <p>Manage your account and preferences</p>
      </div>

      <div className="settings-layout">
        <nav className="settings-nav">
          <button
            className={`nav-btn ${activeTab === 'profile' ? 'active' : ''}`}
            onClick={() => setActiveTab('profile')}
          >
             Profile
          </button>
          <button
            className={`nav-btn ${activeTab === 'security' ? 'active' : ''}`}
            onClick={() => setActiveTab('security')}
          >
             Security
          </button>
          <button
            className={`nav-btn ${activeTab === 'servers' ? 'active' : ''}`}
            onClick={() => setActiveTab('servers')}
          >
             My Servers
          </button>
        </nav>

        <div className="settings-content">
          {message.text && (
            <div className={`message ${message.type}`}>
              {message.text}
              <button onClick={() => setMessage({ type: '', text: '' })}></button>
            </div>
          )}

          {activeTab === 'profile' && (
            <div className="settings-section">
              <h2>Profile Information</h2>
              
              <div className="info-card">
                <div className="info-row">
                  <span className="info-label">Username</span>
                  <span className="info-value">{user?.username}</span>
                </div>
                <div className="info-row">
                  <span className="info-label">Role</span>
                  <span className={`role-badge ${user?.role}`}>{user?.role}</span>
                </div>
                <div className="info-row">
                  <span className="info-label">Direct Add</span>
                  <span className="info-value">
                    {user?.canAddDirectly ? ' Enabled' : ' Requires approval'}
                  </span>
                </div>
              </div>

              <form onSubmit={handleProfileSubmit}>
                <div className="form-group">
                  <label>Email Address</label>
                  <input
                    type="email"
                    value={profileForm.email}
                    onChange={e => setProfileForm({ ...profileForm, email: e.target.value })}
                    placeholder="your@email.com"
                  />
                </div>

                <button type="submit" className="btn btn-primary" disabled={saving}>
                  {saving ? 'Saving...' : 'Update Profile'}
                </button>
              </form>
            </div>
          )}

          {activeTab === 'security' && (
            <div className="settings-section">
              <h2>Change Password</h2>
              
              <form onSubmit={handlePasswordSubmit}>
                <div className="form-group">
                  <label>Current Password</label>
                  <input
                    type="password"
                    value={passwordForm.currentPassword}
                    onChange={e => setPasswordForm({ ...passwordForm, currentPassword: e.target.value })}
                    required
                  />
                </div>

                <div className="form-group">
                  <label>New Password</label>
                  <input
                    type="password"
                    value={passwordForm.newPassword}
                    onChange={e => setPasswordForm({ ...passwordForm, newPassword: e.target.value })}
                    required
                    minLength={8}
                  />
                  <span className="form-hint">Must be at least 8 characters</span>
                </div>

                <div className="form-group">
                  <label>Confirm New Password</label>
                  <input
                    type="password"
                    value={passwordForm.confirmPassword}
                    onChange={e => setPasswordForm({ ...passwordForm, confirmPassword: e.target.value })}
                    required
                  />
                </div>

                <button type="submit" className="btn btn-primary" disabled={saving}>
                  {saving ? 'Changing...' : 'Change Password'}
                </button>
              </form>

              <div className="danger-zone">
                <h3>Sessions</h3>
                <p>Log out of all devices including this one.</p>
                <button className="btn btn-danger" onClick={handleLogoutAllDevices}>
                  Logout All Devices
                </button>
              </div>
            </div>
          )}

          {activeTab === 'servers' && (
            <div className="settings-section">
              <h2>My Assigned Servers</h2>
              
              {user?.servers?.length > 0 ? (
                <div className="server-list">
                  {user.servers.map(server => (
                    <div key={server.id} className="server-item">
                      <div className="server-info">
                        <span className="server-name">
                          {server.name}
                          {server.isPrimary && <span className="primary-badge">Primary</span>}
                        </span>
                        <span className="server-url">{server.url}</span>
                      </div>
                      <div className="server-features">
                        {server.hasRadarr && <span className="feature"> Radarr</span>}
                        {server.hasSonarr && <span className="feature"> Sonarr</span>}
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <div className="empty-state">
                  <span className="empty-icon"></span>
                  <p>No servers assigned to your account</p>
                  <p className="empty-hint">Contact an administrator to be assigned to a Plex server</p>
                </div>
              )}
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default Settings;
