import React from 'react';
import './MediaCard.css';

const TMDB_IMAGE_BASE = 'https://image.tmdb.org/t/p/w500';

function MediaCard({ media, onClick }) {
  const getStatusBadge = () => {
    switch (media.status) {
      case 'available':
        return <span className="status-badge available"> Available</span>;
      case 'downloaded':
        return <span className="status-badge downloaded"> Downloaded</span>;
      case 'queued':
        return <span className="status-badge queued"> Queued</span>;
      case 'missing':
        return <span className="status-badge missing"> Missing</span>;
      case 'requested':
        return <span className="status-badge requested"> Requested</span>;
      default:
        return null;
    }
  };

  return (
    <div className="media-card" onClick={onClick}>
      <div className="card-poster">
        {media.posterPath ? (
          <img 
            src={`${TMDB_IMAGE_BASE}${media.posterPath}`} 
            alt={media.title}
            loading="lazy"
          />
        ) : (
          <div className="no-poster">
            {media.mediaType === 'tv' ? '' : ''}
          </div>
        )}
        
        <div className="card-overlay">
          <span className="media-type">
            {media.mediaType === 'tv' ? 'TV' : 'Movie'}
          </span>
          {getStatusBadge()}
        </div>
        
        <div className="card-hover">
          <span className="view-details">View Details</span>
        </div>
      </div>
      
      <div className="card-info">
        <h3 className="card-title">{media.title}</h3>
        <div className="card-meta">
          {media.year && <span className="card-year">{media.year}</span>}
          {media.voteAverage > 0 && (
            <span className="card-rating">
               {media.voteAverage.toFixed(1)}
            </span>
          )}
        </div>
      </div>
    </div>
  );
}

export default MediaCard;
