import React, { useState, useEffect } from 'react';
import { Outlet, NavLink, useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { requestsApi } from '../services/api';
import './Layout.css';

function Layout() {
  const { user, logout } = useAuth();
  const navigate = useNavigate();
  const [pendingCount, setPendingCount] = useState(0);
  const [menuOpen, setMenuOpen] = useState(false);

  useEffect(() => {
    if (user?.role === 'admin') {
      fetchPendingCount();
      const interval = setInterval(fetchPendingCount, 60000); // Check every minute
      return () => clearInterval(interval);
    }
  }, [user]);

  const fetchPendingCount = async () => {
    try {
      const response = await requestsApi.getPendingCount();
      setPendingCount(response.data.count);
    } catch (error) {
      console.error('Failed to fetch pending count:', error);
    }
  };

  const handleLogout = async () => {
    await logout();
    navigate('/login');
  };

  return (
    <div className="layout">
      <nav className="navbar">
        <div className="nav-brand">
          <span className="brand-icon"></span>
          <span className="brand-text">BigFlix</span>
        </div>

        <button className="menu-toggle" onClick={() => setMenuOpen(!menuOpen)}>
          
        </button>

        <div className={`nav-links ${menuOpen ? 'open' : ''}`}>
          <NavLink to="/" className={({ isActive }) => isActive ? 'nav-link active' : 'nav-link'} end>
            <span className="nav-icon"></span>
            <span>Search</span>
          </NavLink>
          
          <NavLink to="/requests" className={({ isActive }) => isActive ? 'nav-link active' : 'nav-link'}>
            <span className="nav-icon"></span>
            <span>Requests</span>
            {pendingCount > 0 && user?.role === 'admin' && (
              <span className="badge">{pendingCount}</span>
            )}
          </NavLink>
          
          <NavLink to="/library" className={({ isActive }) => isActive ? 'nav-link active' : 'nav-link'}>
            <span className="nav-icon"></span>
            <span>Library</span>
          </NavLink>
          
          {user?.role === 'admin' && (
            <NavLink to="/admin" className={({ isActive }) => isActive ? 'nav-link active' : 'nav-link'}>
              <span className="nav-icon"></span>
              <span>Admin</span>
            </NavLink>
          )}
        </div>

        <div className="nav-user">
          <div className="user-info">
            <span className="user-avatar">{user?.username?.charAt(0).toUpperCase()}</span>
            <span className="user-name">{user?.username}</span>
            {user?.role === 'admin' && <span className="user-badge">Admin</span>}
          </div>
          
          <div className="user-menu">
            <NavLink to="/settings" className="user-menu-item">Settings</NavLink>
            <button onClick={handleLogout} className="user-menu-item logout">Logout</button>
          </div>
        </div>
      </nav>

      <main className="main-content">
        <Outlet />
      </main>
    </div>
  );
}

export default Layout;
