import React, { useState, useEffect } from 'react';
import { useAuth } from '../contexts/AuthContext';
import { searchApi, requestsApi } from '../services/api';
import MediaCard from '../components/MediaCard';
import MediaModal from '../components/MediaModal';
import './Search.css';

function Search() {
  const { user } = useAuth();
  const [query, setQuery] = useState('');
  const [searchType, setSearchType] = useState('multi');
  const [results, setResults] = useState([]);
  const [loading, setLoading] = useState(false);
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  const [selectedMedia, setSelectedMedia] = useState(null);
  const [message, setMessage] = useState({ type: '', text: '' });

  const handleSearch = async (e) => {
    e?.preventDefault();
    if (!query.trim()) return;

    setLoading(true);
    setMessage({ type: '', text: '' });
    
    try {
      let response;
      switch (searchType) {
        case 'movies':
          response = await searchApi.searchMovies(query, 1);
          break;
        case 'tv':
          response = await searchApi.searchTv(query, 1);
          break;
        default:
          response = await searchApi.searchMulti(query, 1);
      }
      
      setResults(response.data.results);
      setTotalPages(response.data.totalPages);
      setPage(1);
    } catch (error) {
      console.error('Search error:', error);
      setMessage({ type: 'error', text: 'Search failed. Please try again.' });
    } finally {
      setLoading(false);
    }
  };

  const loadMore = async () => {
    if (page >= totalPages || loading) return;
    
    setLoading(true);
    try {
      const nextPage = page + 1;
      let response;
      switch (searchType) {
        case 'movies':
          response = await searchApi.searchMovies(query, nextPage);
          break;
        case 'tv':
          response = await searchApi.searchTv(query, nextPage);
          break;
        default:
          response = await searchApi.searchMulti(query, nextPage);
      }
      
      setResults(prev => [...prev, ...response.data.results]);
      setPage(nextPage);
    } catch (error) {
      console.error('Load more error:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleRequest = async (media, seasons = null) => {
    try {
      const requestData = {
        tmdbId: media.id,
        contentType: media.mediaType,
        title: media.title,
        year: media.year,
        overview: media.overview,
        posterPath: media.posterPath,
        seasons
      };
      
      const response = await requestsApi.createRequest(requestData);
      
      if (response.data.status === 'added') {
        setMessage({ type: 'success', text: `${media.title} added to download queue!` });
      } else {
        setMessage({ type: 'success', text: `Request for ${media.title} submitted for approval.` });
      }
      
      setSelectedMedia(null);
      
      // Refresh search results to update status
      handleSearch();
    } catch (error) {
      setMessage({ 
        type: 'error', 
        text: error.response?.data?.error || 'Failed to submit request' 
      });
    }
  };

  return (
    <div className="search-page">
      <div className="search-header">
        <h1>Discover</h1>
        <p>Search for movies and TV shows to add to your library</p>
      </div>

      <form onSubmit={handleSearch} className="search-form">
        <div className="search-input-group">
          <input
            type="text"
            value={query}
            onChange={(e) => setQuery(e.target.value)}
            placeholder="Search for movies or TV shows..."
            className="search-input"
          />
          <button type="submit" className="search-btn" disabled={loading}>
            {loading ? '...' : ''}
          </button>
        </div>
        
        <div className="search-filters">
          <button
            type="button"
            className={`filter-btn ${searchType === 'multi' ? 'active' : ''}`}
            onClick={() => setSearchType('multi')}
          >
            All
          </button>
          <button
            type="button"
            className={`filter-btn ${searchType === 'movies' ? 'active' : ''}`}
            onClick={() => setSearchType('movies')}
          >
             Movies
          </button>
          <button
            type="button"
            className={`filter-btn ${searchType === 'tv' ? 'active' : ''}`}
            onClick={() => setSearchType('tv')}
          >
             TV Shows
          </button>
        </div>
      </form>

      {message.text && (
        <div className={`message ${message.type}`}>
          {message.text}
        </div>
      )}

      {results.length > 0 ? (
        <>
          <div className="results-grid">
            {results.map(media => (
              <MediaCard
                key={`${media.mediaType}-${media.id}`}
                media={media}
                onClick={() => setSelectedMedia(media)}
              />
            ))}
          </div>
          
          {page < totalPages && (
            <div className="load-more">
              <button 
                className="load-more-btn" 
                onClick={loadMore}
                disabled={loading}
              >
                {loading ? 'Loading...' : 'Load More'}
              </button>
            </div>
          )}
        </>
      ) : (
        <div className="no-results">
          {query ? (
            <p>No results found for "{query}"</p>
          ) : (
            <div className="search-prompt">
              <span className="prompt-icon"></span>
              <p>Search for movies and TV shows to get started</p>
            </div>
          )}
        </div>
      )}

      {selectedMedia && (
        <MediaModal
          media={selectedMedia}
          onClose={() => setSelectedMedia(null)}
          onRequest={handleRequest}
          canAddDirectly={user?.canAddDirectly}
        />
      )}
    </div>
  );
}

export default Search;
