import React, { useState, useEffect } from 'react';
import { searchApi } from '../services/api';
import './MediaModal.css';

const TMDB_IMAGE_BASE = 'https://image.tmdb.org/t/p/w500';
const TMDB_BACKDROP_BASE = 'https://image.tmdb.org/t/p/original';

function MediaModal({ media, onClose, onRequest, canAddDirectly }) {
  const [details, setDetails] = useState(null);
  const [loading, setLoading] = useState(true);
  const [selectedSeasons, setSelectedSeasons] = useState([]);
  const [requesting, setRequesting] = useState(false);

  useEffect(() => {
    fetchDetails();
  }, [media]);

  const fetchDetails = async () => {
    try {
      const response = media.mediaType === 'tv'
        ? await searchApi.getTvDetails(media.id)
        : await searchApi.getMovieDetails(media.id);
      setDetails(response.data);
      
      // Pre-select all seasons for TV shows
      if (response.data.seasons) {
        setSelectedSeasons(
          response.data.seasons
            .filter(s => s.seasonNumber > 0) // Exclude specials
            .map(s => s.seasonNumber)
        );
      }
    } catch (error) {
      console.error('Failed to fetch details:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleSeasonToggle = (seasonNumber) => {
    setSelectedSeasons(prev => 
      prev.includes(seasonNumber)
        ? prev.filter(s => s !== seasonNumber)
        : [...prev, seasonNumber]
    );
  };

  const handleRequest = async () => {
    setRequesting(true);
    try {
      await onRequest(
        details || media, 
        media.mediaType === 'tv' ? selectedSeasons : null
      );
    } finally {
      setRequesting(false);
    }
  };

  const getStatusInfo = () => {
    if (details?.plexAvailable) {
      return {
        text: `Available on ${details.plexServers?.join(', ') || 'Plex'}`,
        className: 'available'
      };
    }
    switch (details?.status || media.status) {
      case 'downloaded':
        return { text: 'Downloaded', className: 'downloaded' };
      case 'queued':
        return { text: 'In Download Queue', className: 'queued' };
      case 'missing':
        return { text: 'Missing Files', className: 'missing' };
      case 'requested':
        return { text: 'Request Pending', className: 'requested' };
      default:
        return { text: 'Not Available', className: 'not-available' };
    }
  };

  const statusInfo = getStatusInfo();
  const isAvailable = details?.plexAvailable || details?.status === 'downloaded';
  const isPending = details?.status === 'requested' || details?.inRssFeed;

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal-content" onClick={e => e.stopPropagation()}>
        <button className="modal-close" onClick={onClose}></button>
        
        {loading ? (
          <div className="modal-loading">
            <div className="spinner"></div>
          </div>
        ) : (
          <>
            {/* Backdrop */}
            {(details?.backdropPath || media.backdropPath) && (
              <div 
                className="modal-backdrop"
                style={{ 
                  backgroundImage: `url(${TMDB_BACKDROP_BASE}${details?.backdropPath || media.backdropPath})` 
                }}
              />
            )}
            
            <div className="modal-body">
              {/* Poster */}
              <div className="modal-poster">
                {(details?.posterPath || media.posterPath) ? (
                  <img 
                    src={`${TMDB_IMAGE_BASE}${details?.posterPath || media.posterPath}`}
                    alt={details?.title || media.title}
                  />
                ) : (
                  <div className="no-poster">
                    {media.mediaType === 'tv' ? '' : ''}
                  </div>
                )}
              </div>
              
              {/* Info */}
              <div className="modal-info">
                <div className="modal-header">
                  <h2>{details?.title || media.title}</h2>
                  <div className="modal-meta">
                    <span className="meta-type">
                      {media.mediaType === 'tv' ? ' TV Series' : ' Movie'}
                    </span>
                    {(details?.year || media.year) && (
                      <span className="meta-year">{details?.year || media.year}</span>
                    )}
                    {(details?.voteAverage || media.voteAverage) > 0 && (
                      <span className="meta-rating">
                         {(details?.voteAverage || media.voteAverage).toFixed(1)}
                      </span>
                    )}
                    {media.mediaType === 'tv' && details?.numberOfSeasons && (
                      <span className="meta-seasons">
                        {details.numberOfSeasons} Season{details.numberOfSeasons > 1 ? 's' : ''}
                      </span>
                    )}
                  </div>
                </div>
                
                <div className={`status-banner ${statusInfo.className}`}>
                  {statusInfo.text}
                </div>
                
                <p className="modal-overview">
                  {details?.overview || media.overview || 'No description available.'}
                </p>
                
                {/* Season Selection for TV Shows */}
                {media.mediaType === 'tv' && details?.seasons && !isAvailable && !isPending && (
                  <div className="season-selection">
                    <h3>Select Seasons</h3>
                    <div className="season-grid">
                      {details.seasons
                        .filter(s => s.seasonNumber > 0)
                        .map(season => (
                          <label 
                            key={season.seasonNumber}
                            className={`season-option ${selectedSeasons.includes(season.seasonNumber) ? 'selected' : ''}`}
                          >
                            <input
                              type="checkbox"
                              checked={selectedSeasons.includes(season.seasonNumber)}
                              onChange={() => handleSeasonToggle(season.seasonNumber)}
                            />
                            <span className="season-name">Season {season.seasonNumber}</span>
                            <span className="season-episodes">{season.episodeCount} episodes</span>
                          </label>
                        ))
                      }
                    </div>
                    <div className="season-actions">
                      <button 
                        className="btn-link"
                        onClick={() => setSelectedSeasons(
                          details.seasons
                            .filter(s => s.seasonNumber > 0)
                            .map(s => s.seasonNumber)
                        )}
                      >
                        Select All
                      </button>
                      <button 
                        className="btn-link"
                        onClick={() => setSelectedSeasons([])}
                      >
                        Clear All
                      </button>
                    </div>
                  </div>
                )}
                
                {/* Action Buttons */}
                <div className="modal-actions">
                  {isAvailable ? (
                    <button className="btn btn-success" disabled>
                       Already Available
                    </button>
                  ) : isPending ? (
                    <button className="btn btn-secondary" disabled>
                       Request Pending
                    </button>
                  ) : (
                    <button 
                      className="btn btn-primary"
                      onClick={handleRequest}
                      disabled={requesting || (media.mediaType === 'tv' && selectedSeasons.length === 0)}
                    >
                      {requesting ? 'Submitting...' : (
                        canAddDirectly 
                          ? `Add to Download Queue`
                          : `Request ${media.mediaType === 'tv' ? 'TV Show' : 'Movie'}`
                      )}
                    </button>
                  )}
                  
                  <a 
                    href={`https://www.themoviedb.org/${media.mediaType}/${media.id}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="btn btn-secondary"
                  >
                    View on TMDB
                  </a>
                </div>
              </div>
            </div>
          </>
        )}
      </div>
    </div>
  );
}

export default MediaModal;
