import React, { useState, useRef } from 'react';
import { useAuth } from '../contexts/AuthContext';
import { setupApi } from '../services/api';
import './SetupWizard.css';

function SetupWizard() {
  const { completeSetup } = useAuth();
  const [step, setStep] = useState(1);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const fileInputRef = useRef(null);

  // Restore state
  const [restoreMode, setRestoreMode] = useState(false);
  const [backupPreview, setBackupPreview] = useState(null);
  const [backupData, setBackupData] = useState(null);

  // Form data
  const [adminForm, setAdminForm] = useState({
    username: '',
    password: '',
    confirmPassword: '',
    email: ''
  });

  const [tmdbForm, setTmdbForm] = useState({
    apiKey: ''
  });

  const [serverForm, setServerForm] = useState({
    name: '',
    plexUrl: '',
    plexToken: '',
    librarySectionId: '1',
    radarrUrl: '',
    radarrApiKey: '',
    sonarrUrl: '',
    sonarrApiKey: ''
  });

  const [testResults, setTestResults] = useState({
    plex: null,
    radarr: null,
    sonarr: null,
    tmdb: null
  });

  const handleAdminSubmit = async (e) => {
    e.preventDefault();
    setError('');
    
    if (adminForm.password !== adminForm.confirmPassword) {
      setError('Passwords do not match');
      return;
    }
    
    if (adminForm.password.length < 8) {
      setError('Password must be at least 8 characters');
      return;
    }

    setLoading(true);
    try {
      await setupApi.createAdmin({
        username: adminForm.username,
        password: adminForm.password,
        email: adminForm.email
      });
      setSuccess('Admin account created successfully');
      setTimeout(() => {
        setStep(2);
        setSuccess('');
      }, 1000);
    } catch (err) {
      setError(err.response?.data?.error || 'Failed to create admin account');
    } finally {
      setLoading(false);
    }
  };

  const handleTmdbSubmit = async (e) => {
    e.preventDefault();
    setError('');

    setLoading(true);
    try {
      // Test the API key first
      const testResult = await setupApi.testTmdb({ apiKey: tmdbForm.apiKey });
      setTestResults(prev => ({ ...prev, tmdb: testResult.data }));
      
      // Save it
      await setupApi.configureTmdb({ apiKey: tmdbForm.apiKey });
      setSuccess('TMDB API configured successfully');
      setTimeout(() => {
        setStep(3);
        setSuccess('');
      }, 1000);
    } catch (err) {
      setTestResults(prev => ({ ...prev, tmdb: { success: false, error: err.response?.data?.error || err.message } }));
      setError(err.response?.data?.error || 'Invalid TMDB API key');
    } finally {
      setLoading(false);
    }
  };

  const testPlex = async () => {
    setLoading(true);
    try {
      const result = await setupApi.testPlex({
        url: serverForm.plexUrl,
        token: serverForm.plexToken
      });
      setTestResults(prev => ({ ...prev, plex: result.data }));
    } catch (err) {
      setTestResults(prev => ({ ...prev, plex: { success: false, error: err.response?.data?.error || err.message } }));
    } finally {
      setLoading(false);
    }
  };

  const testRadarr = async () => {
    if (!serverForm.radarrUrl || !serverForm.radarrApiKey) return;
    setLoading(true);
    try {
      const result = await setupApi.testRadarr({
        url: serverForm.radarrUrl,
        apiKey: serverForm.radarrApiKey
      });
      setTestResults(prev => ({ ...prev, radarr: result.data }));
    } catch (err) {
      setTestResults(prev => ({ ...prev, radarr: { success: false, error: err.response?.data?.error || err.message } }));
    } finally {
      setLoading(false);
    }
  };

  const testSonarr = async () => {
    if (!serverForm.sonarrUrl || !serverForm.sonarrApiKey) return;
    setLoading(true);
    try {
      const result = await setupApi.testSonarr({
        url: serverForm.sonarrUrl,
        apiKey: serverForm.sonarrApiKey
      });
      setTestResults(prev => ({ ...prev, sonarr: result.data }));
    } catch (err) {
      setTestResults(prev => ({ ...prev, sonarr: { success: false, error: err.response?.data?.error || err.message } }));
    } finally {
      setLoading(false);
    }
  };

  const handleServerSubmit = async (e) => {
    e.preventDefault();
    setError('');

    if (!testResults.plex?.success) {
      setError('Please test and verify Plex connection first');
      return;
    }

    setLoading(true);
    try {
      await setupApi.configureServer(serverForm);
      setSuccess('Server configured successfully');
      setTimeout(() => {
        setStep(4);
        setSuccess('');
      }, 1000);
    } catch (err) {
      setError(err.response?.data?.error || 'Failed to configure server');
    } finally {
      setLoading(false);
    }
  };

  // Handle backup file selection
  const handleFileSelect = async (e) => {
    const file = e.target.files[0];
    if (!file) return;

    setError('');
    setLoading(true);

    try {
      const text = await file.text();
      const backup = JSON.parse(text);

      // Preview the backup
      const response = await setupApi.previewBackup({ backup });
      setBackupPreview(response.data);
      setBackupData(backup);
    } catch (err) {
      setError(err.response?.data?.error || 'Failed to read backup file. Make sure it\'s a valid BigFlix backup.');
      setBackupPreview(null);
      setBackupData(null);
    } finally {
      setLoading(false);
    }
  };

  // Handle restore from backup
  const handleRestore = async () => {
    if (!backupData) return;

    setError('');
    setLoading(true);

    try {
      const response = await setupApi.restoreBackup({ backup: backupData });
      setSuccess(`Restored successfully: ${response.data.imported.servers} servers, ${response.data.imported.rssItems} RSS items`);
      
      // Skip to complete step
      setTimeout(() => {
        setStep(4);
        setSuccess('');
        setRestoreMode(false);
      }, 2000);
    } catch (err) {
      setError(err.response?.data?.error || 'Failed to restore backup');
    } finally {
      setLoading(false);
    }
  };

  const handleComplete = async () => {
    setLoading(true);
    try {
      const response = await setupApi.completeSetup();
      await completeSetup(response.data.token, response.data.user);
    } catch (err) {
      setError(err.response?.data?.error || 'Failed to complete setup');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="setup-wizard">
      <div className="setup-container">
        <div className="setup-header">
          <h1> BigFlix Setup</h1>
          <p>Let's configure your media management platform</p>
        </div>

        <div className="setup-progress">
          {[1, 2, 3, 4].map(s => (
            <div key={s} className={`progress-step ${step >= s ? 'active' : ''} ${step === s ? 'current' : ''}`}>
              <div className="step-number">{step > s ? '' : s}</div>
              <div className="step-label">
                {s === 1 && 'Admin Account'}
                {s === 2 && 'TMDB API'}
                {s === 3 && 'Plex Server'}
                {s === 4 && 'Complete'}
              </div>
            </div>
          ))}
        </div>

        {error && <div className="alert alert-error">{error}</div>}
        {success && <div className="alert alert-success">{success}</div>}

        <div className="setup-content">
          {/* Step 1: Admin Account */}
          {step === 1 && (
            <form onSubmit={handleAdminSubmit} className="setup-form">
              <h2>Create Administrator Account</h2>
              <p>This will be the first admin user with full access to the system.</p>

              <div className="form-group">
                <label>Username</label>
                <input
                  type="text"
                  value={adminForm.username}
                  onChange={(e) => setAdminForm({ ...adminForm, username: e.target.value })}
                  placeholder="Enter username"
                  required
                  minLength={3}
                />
              </div>

              <div className="form-group">
                <label>Email (optional)</label>
                <input
                  type="email"
                  value={adminForm.email}
                  onChange={(e) => setAdminForm({ ...adminForm, email: e.target.value })}
                  placeholder="Enter email"
                />
              </div>

              <div className="form-group">
                <label>Password</label>
                <input
                  type="password"
                  value={adminForm.password}
                  onChange={(e) => setAdminForm({ ...adminForm, password: e.target.value })}
                  placeholder="Enter password (min 8 characters)"
                  required
                  minLength={8}
                />
              </div>

              <div className="form-group">
                <label>Confirm Password</label>
                <input
                  type="password"
                  value={adminForm.confirmPassword}
                  onChange={(e) => setAdminForm({ ...adminForm, confirmPassword: e.target.value })}
                  placeholder="Confirm password"
                  required
                />
              </div>

              <button type="submit" className="btn btn-primary" disabled={loading}>
                {loading ? 'Creating...' : 'Create Account'}
              </button>
            </form>
          )}

          {/* Step 1.5: Restore Option (shown after admin creation via restoreMode) */}
          {step === 2 && restoreMode && (
            <div className="setup-form">
              <h2>Restore from Backup</h2>
              <p>Upload a BigFlix backup file to restore your previous configuration.</p>

              <div className="restore-upload-area">
                <input
                  type="file"
                  ref={fileInputRef}
                  onChange={handleFileSelect}
                  accept=".json"
                  style={{ display: 'none' }}
                />
                <button 
                  type="button" 
                  className="btn btn-secondary btn-large"
                  onClick={() => fileInputRef.current?.click()}
                  disabled={loading}
                >
                   Select Backup File
                </button>
              </div>

              {backupPreview && (
                <div className="backup-preview">
                  <h3>Backup Contents</h3>
                  <div className="backup-info">
                    <p><strong>Export Date:</strong> {new Date(backupPreview.exportDate).toLocaleString()}</p>
                    <p><strong>Version:</strong> {backupPreview.version}</p>
                  </div>
                  <ul className="backup-summary">
                    <li> {backupPreview.summary.servers} server(s)</li>
                    <li> {backupPreview.summary.users} user(s)</li>
                    <li> {backupPreview.summary.rssItems} RSS item(s)</li>
                    <li> {backupPreview.summary.requests} request(s)</li>
                    <li> {backupPreview.summary.settings} setting(s)</li>
                  </ul>
                  <div className="backup-warning">
                    <strong>Note:</strong> Only servers, settings, and RSS items will be restored. 
                    Users (except your admin account) will need to be recreated.
                  </div>
                  <button 
                    type="button" 
                    className="btn btn-primary"
                    onClick={handleRestore}
                    disabled={loading}
                  >
                    {loading ? 'Restoring...' : 'Restore Backup'}
                  </button>
                </div>
              )}

              <div className="form-actions" style={{ marginTop: '2rem' }}>
                <button 
                  type="button" 
                  className="btn btn-secondary" 
                  onClick={() => {
                    setRestoreMode(false);
                    setBackupPreview(null);
                    setBackupData(null);
                    setError('');
                  }}
                >
                   Back to Fresh Setup
                </button>
              </div>
            </div>
          )}

          {/* Step 2: TMDB API */}
          {step === 2 && !restoreMode && (
            <form onSubmit={handleTmdbSubmit} className="setup-form">
              <h2>Configure TMDB API</h2>
              
              {/* Restore option banner */}
              <div className="restore-option-banner">
                <span>Have a backup?</span>
                <button 
                  type="button" 
                  className="btn btn-link"
                  onClick={() => setRestoreMode(true)}
                >
                  Restore from backup 
                </button>
              </div>

              <p>
                Get your free API key from{' '}
                <a href="https://www.themoviedb.org/settings/api" target="_blank" rel="noopener noreferrer">
                  themoviedb.org
                </a>
              </p>

              <div className="form-group">
                <label>TMDB API Key</label>
                <input
                  type="text"
                  value={tmdbForm.apiKey}
                  onChange={(e) => setTmdbForm({ ...tmdbForm, apiKey: e.target.value })}
                  placeholder="Enter your TMDB API key"
                  required
                />
              </div>

              {testResults.tmdb && (
                <div className={`test-result ${testResults.tmdb.success ? 'success' : 'error'}`}>
                  {testResults.tmdb.success ? ' API key valid' : ` ${testResults.tmdb.error}`}
                </div>
              )}

              <div className="form-actions">
                <button type="button" className="btn btn-secondary" onClick={() => setStep(1)}>
                  Back
                </button>
                <button type="submit" className="btn btn-primary" disabled={loading}>
                  {loading ? 'Validating...' : 'Continue'}
                </button>
              </div>
            </form>
          )}

          {/* Step 3: Server Configuration */}
          {step === 3 && (
            <form onSubmit={handleServerSubmit} className="setup-form">
              <h2>Configure Plex Server</h2>
              <p>Connect your first Plex server and optionally configure Radarr/Sonarr.</p>

              <div className="form-section">
                <h3>Plex Server</h3>
                
                <div className="form-group">
                  <label>Server Name</label>
                  <input
                    type="text"
                    value={serverForm.name}
                    onChange={(e) => setServerForm({ ...serverForm, name: e.target.value })}
                    placeholder="e.g., Main Server"
                    required
                  />
                </div>

                <div className="form-group">
                  <label>Plex URL</label>
                  <input
                    type="url"
                    value={serverForm.plexUrl}
                    onChange={(e) => setServerForm({ ...serverForm, plexUrl: e.target.value })}
                    placeholder="e.g., http://192.168.1.100:32400"
                    required
                  />
                </div>

                <div className="form-group">
                  <label>Plex Token</label>
                  <input
                    type="password"
                    value={serverForm.plexToken}
                    onChange={(e) => setServerForm({ ...serverForm, plexToken: e.target.value })}
                    placeholder="Your Plex token"
                    required
                  />
                  <small>
                    <a href="https://support.plex.tv/articles/204059436-finding-an-authentication-token-x-plex-token/" 
                       target="_blank" rel="noopener noreferrer">
                      How to find your Plex token
                    </a>
                  </small>
                </div>

                <button type="button" className="btn btn-secondary" onClick={testPlex} disabled={loading}>
                  Test Connection
                </button>

                {testResults.plex && (
                  <div className={`test-result ${testResults.plex.success ? 'success' : 'error'}`}>
                    {testResults.plex.success 
                      ? ` Connected to ${testResults.plex.serverName} (v${testResults.plex.version})`
                      : ` ${testResults.plex.error}`
                    }
                  </div>
                )}
              </div>

              <div className="form-section">
                <h3>Radarr (Optional)</h3>
                
                <div className="form-row">
                  <div className="form-group">
                    <label>Radarr URL</label>
                    <input
                      type="url"
                      value={serverForm.radarrUrl}
                      onChange={(e) => setServerForm({ ...serverForm, radarrUrl: e.target.value })}
                      placeholder="e.g., http://192.168.1.100:7878"
                    />
                  </div>
                  
                  <div className="form-group">
                    <label>Radarr API Key</label>
                    <input
                      type="password"
                      value={serverForm.radarrApiKey}
                      onChange={(e) => setServerForm({ ...serverForm, radarrApiKey: e.target.value })}
                      placeholder="Radarr API key"
                    />
                  </div>
                </div>

                {serverForm.radarrUrl && serverForm.radarrApiKey && (
                  <>
                    <button type="button" className="btn btn-secondary btn-small" onClick={testRadarr} disabled={loading}>
                      Test Radarr
                    </button>
                    {testResults.radarr && (
                      <div className={`test-result ${testResults.radarr.success ? 'success' : 'error'}`}>
                        {testResults.radarr.success 
                          ? ` Radarr v${testResults.radarr.version}`
                          : ` ${testResults.radarr.error}`
                        }
                      </div>
                    )}
                  </>
                )}
              </div>

              <div className="form-section">
                <h3>Sonarr (Optional)</h3>
                
                <div className="form-row">
                  <div className="form-group">
                    <label>Sonarr URL</label>
                    <input
                      type="url"
                      value={serverForm.sonarrUrl}
                      onChange={(e) => setServerForm({ ...serverForm, sonarrUrl: e.target.value })}
                      placeholder="e.g., http://192.168.1.100:8989"
                    />
                  </div>
                  
                  <div className="form-group">
                    <label>Sonarr API Key</label>
                    <input
                      type="password"
                      value={serverForm.sonarrApiKey}
                      onChange={(e) => setServerForm({ ...serverForm, sonarrApiKey: e.target.value })}
                      placeholder="Sonarr API key"
                    />
                  </div>
                </div>

                {serverForm.sonarrUrl && serverForm.sonarrApiKey && (
                  <>
                    <button type="button" className="btn btn-secondary btn-small" onClick={testSonarr} disabled={loading}>
                      Test Sonarr
                    </button>
                    {testResults.sonarr && (
                      <div className={`test-result ${testResults.sonarr.success ? 'success' : 'error'}`}>
                        {testResults.sonarr.success 
                          ? ` Sonarr v${testResults.sonarr.version}`
                          : ` ${testResults.sonarr.error}`
                        }
                      </div>
                    )}
                  </>
                )}
              </div>

              <div className="form-actions">
                <button type="button" className="btn btn-secondary" onClick={() => setStep(2)}>
                  Back
                </button>
                <button type="submit" className="btn btn-primary" disabled={loading || !testResults.plex?.success}>
                  {loading ? 'Saving...' : 'Continue'}
                </button>
              </div>
            </form>
          )}

          {/* Step 4: Complete */}
          {step === 4 && (
            <div className="setup-complete">
              <div className="complete-icon"></div>
              <h2>Setup Complete!</h2>
              <p>Your BigFlix is ready to use.</p>
              
              <div className="setup-summary">
                <h3>Configuration Summary</h3>
                <ul>
                  <li> Administrator account created</li>
                  <li> TMDB API configured</li>
                  <li> Plex server connected</li>
                  {serverForm.radarrUrl && <li> Radarr configured</li>}
                  {serverForm.sonarrUrl && <li> Sonarr configured</li>}
                </ul>
              </div>

              <button className="btn btn-primary btn-large" onClick={handleComplete} disabled={loading}>
                {loading ? 'Starting...' : 'Start Using BigFlix'}
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default SetupWizard;
