import React, { useState, useEffect } from 'react';
import { useAuth } from '../contexts/AuthContext';
import { rssApi } from '../services/api';
import './Library.css';

const TMDB_IMAGE_BASE = 'https://image.tmdb.org/t/p/w200';

function Library() {
  const { user } = useAuth();
  const [items, setItems] = useState([]);
  const [loading, setLoading] = useState(true);
  const [filter, setFilter] = useState('all');
  const [message, setMessage] = useState({ type: '', text: '' });

  useEffect(() => {
    fetchItems();
  }, [filter]);

  const fetchItems = async () => {
    setLoading(true);
    try {
      const params = filter !== 'all' ? { type: filter } : {};
      const response = await rssApi.getItems(params);
      setItems(response.data);
    } catch (error) {
      console.error('Failed to fetch library:', error);
      setMessage({ type: 'error', text: 'Failed to load library' });
    } finally {
      setLoading(false);
    }
  };

  const handleRemove = async (item) => {
    if (!window.confirm(`Remove "${item.title}" from the download queue?`)) return;
    
    try {
      await rssApi.removeItem(item.id);
      setMessage({ type: 'success', text: `${item.title} removed from download queue` });
      fetchItems();
    } catch (error) {
      setMessage({ type: 'error', text: error.response?.data?.error || 'Failed to remove item' });
    }
  };

  return (
    <div className="library-page">
      <div className="page-header">
        <h1>Download Queue</h1>
        <p>Content pending download by Radarr and Sonarr</p>
      </div>

      <div className="filter-bar">
        <button
          className={`filter-btn ${filter === 'all' ? 'active' : ''}`}
          onClick={() => setFilter('all')}
        >
          All
        </button>
        <button
          className={`filter-btn ${filter === 'movie' ? 'active' : ''}`}
          onClick={() => setFilter('movie')}
        >
           Movies
        </button>
        <button
          className={`filter-btn ${filter === 'tv' ? 'active' : ''}`}
          onClick={() => setFilter('tv')}
        >
           TV Shows
        </button>
      </div>

      {message.text && (
        <div className={`message ${message.type}`}>
          {message.text}
        </div>
      )}

      {loading ? (
        <div className="loading">
          <div className="spinner"></div>
        </div>
      ) : items.length === 0 ? (
        <div className="empty-state">
          <span className="empty-icon"></span>
          <p>No items in the download queue</p>
          <p className="empty-hint">Search for movies and TV shows to add them here</p>
        </div>
      ) : (
        <div className="library-grid">
          {items.map(item => (
            <div key={item.id} className="library-card">
              <div className="card-poster">
                {item.posterPath ? (
                  <img 
                    src={`${TMDB_IMAGE_BASE}${item.posterPath}`}
                    alt={item.title}
                  />
                ) : (
                  <div className="no-poster">
                    {item.contentType === 'tv' ? '' : ''}
                  </div>
                )}
                
                <div className="card-overlay">
                  <span className="media-type">
                    {item.contentType === 'tv' ? 'TV' : 'Movie'}
                  </span>
                </div>

                <button 
                  className="remove-btn"
                  onClick={() => handleRemove(item)}
                  title="Remove from queue"
                >
                  
                </button>
              </div>
              
              <div className="card-info">
                <h3 className="card-title">{item.title}</h3>
                <div className="card-meta">
                  {item.year && <span>{item.year}</span>}
                  {item.seasons && (
                    <span>S{item.seasons.join(', S')}</span>
                  )}
                </div>
                <div className="card-details">
                  <span>Added by {item.addedBy}</span>
                  <span>{item.serverName}</span>
                </div>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}

export default Library;
