import React, { useState } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { AuthProvider, useAuth } from './contexts/AuthContext';
import Layout from './components/Layout';
import IntroAnimation from './components/IntroAnimation';
import SetupWizard from './pages/SetupWizard';
import Login from './pages/Login';
import Search from './pages/Search';
import Requests from './pages/Requests';
import Library from './pages/Library';
import Admin from './pages/Admin';
import Settings from './pages/Settings';
import './App.css';

// Protected route component
function ProtectedRoute({ children, adminOnly = false }) {
  const { user, loading } = useAuth();
  
  if (loading) {
    return <div className="loading-screen">Loading...</div>;
  }
  
  if (!user) {
    return <Navigate to="/login" replace />;
  }
  
  if (adminOnly && user.role !== 'admin') {
    return <Navigate to="/" replace />;
  }
  
  return children;
}

// Setup required route
function SetupRoute({ children }) {
  const { setupRequired, loading } = useAuth();
  
  if (loading) {
    return <div className="loading-screen">Loading...</div>;
  }
  
  if (setupRequired) {
    return <Navigate to="/setup" replace />;
  }
  
  return children;
}

function AppRoutes() {
  const { setupRequired, user, loading } = useAuth();
  const [introComplete, setIntroComplete] = useState(false);
  
  // Show intro animation first (only once per session)
  if (!introComplete && !sessionStorage.getItem('bigflix-intro-seen')) {
    return <IntroAnimation onComplete={() => setIntroComplete(true)} />;
  }
  
  if (loading) {
    return <div className="loading-screen">
      <div className="spinner"></div>
      <p>Loading BigFlix...</p>
    </div>;
  }
  
  return (
    <Routes>
      {/* Setup wizard - only accessible when not configured */}
      <Route 
        path="/setup" 
        element={
          setupRequired ? <SetupWizard /> : <Navigate to="/" replace />
        } 
      />
      
      {/* Login - accessible when not logged in */}
      <Route 
        path="/login" 
        element={
          <SetupRoute>
            {user ? <Navigate to="/" replace /> : <Login />}
          </SetupRoute>
        } 
      />
      
      {/* Protected routes */}
      <Route path="/" element={
        <ProtectedRoute>
          <Layout />
        </ProtectedRoute>
      }>
        <Route index element={<Search />} />
        <Route path="requests" element={<Requests />} />
        <Route path="library" element={<Library />} />
        <Route path="settings" element={<Settings />} />
        <Route path="admin/*" element={
          <ProtectedRoute adminOnly>
            <Admin />
          </ProtectedRoute>
        } />
      </Route>
      
      {/* Catch all */}
      <Route path="*" element={<Navigate to="/" replace />} />
    </Routes>
  );
}

function App() {
  return (
    <Router>
      <AuthProvider>
        <AppRoutes />
      </AuthProvider>
    </Router>
  );
}

export default App;